import React from 'react';
import ReactDOM from 'react-dom';
import { pickBy, identity } from 'lodash';
import { IntlProvider } from 'react-intl';
import { enLocale, itLocale } from 'locale';

import { createWidgetEvent } from 'utils/CustomEventUtils';
import PreviewControlBar from 'components/preview-control-bar/PreviewControlBar';

export const defaultScreenSizes = {
  DESKTOP: '100%x100%',
  MOBILE: '360x640',
  TABLET: '768x1024',
};

const ATTRIBUTES = {
  appBuilderDomain: 'app-builder-domain',
  resolutionWidth: 'resolution-width',
  resolutionHeight: 'resolution-height',
  commentsOpened: 'comments-opened',
  languages: 'languages',
  currentLang: 'current-lang',
  sizeDesktop: 'size-desktop',
  sizeTablet: 'size-tablet',
  sizeMobile: 'size-mobile',
  userLogged: 'user-logged',
  locale: 'locale',
  hasPermissionReview: 'has-permission-review'
};

export const PreviewControlBarEvent = {
  LOGOUT_CLICKED: 'preview-control-bar/on-logout-clicked',
  COMMENT_TOGGLE: 'preview-control-bar/on-comment-toggled',
  RESOLUTION_CHANGE: 'preview-control-bar/on-resolution-change',
  SHARE_CLICKED: 'preview-control-bar/on-share-clicked',
  CHANGE_LANGUAGE: 'preview-control-bar/on-change-language',
};

window.PreviewControlBarEvent = PreviewControlBarEvent;

class PreviewControlBarElement extends HTMLElement {
  constructor(props) {
    super(props);

    this.handleCommentsToggle = this.handleCommentsToggle.bind(this);
    this.updateCommentsClose = this.updateCommentsClose.bind(this);

    this.onLogoutClicked = createWidgetEvent(this, PreviewControlBarEvent.LOGOUT_CLICKED);
    this.onCommentsClicked = createWidgetEvent(this, PreviewControlBarEvent.COMMENT_TOGGLE);
    this.onChangeResolution = createWidgetEvent(this, PreviewControlBarEvent.RESOLUTION_CHANGE);
    this.onShareClicked = createWidgetEvent(this, PreviewControlBarEvent.SHARE_CLICKED);
    this.onChangeLanguage = createWidgetEvent(this, PreviewControlBarEvent.CHANGE_LANGUAGE);
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  attributeChangedCallback(attribute, oldValue, newValue) {
    if (!Object.values(ATTRIBUTES).includes(attribute)) {
      throw new Error(`Untracked changed attribute: ${attribute}`);
    }
    if (newValue !== oldValue) {
      this.render();
    }
  }

  updateCommentsClose() {
    this.setAttribute(ATTRIBUTES.commentsOpened, false);
  }

  connectedCallback() {
    setTimeout(() => this.render(), 500);
  }

  extractScreenSizeAttribute() {
    return [ATTRIBUTES.sizeDesktop, ATTRIBUTES.sizeMobile, ATTRIBUTES.sizeTablet].reduce((acc, sizeAttr) => {
      const sizeType = sizeAttr.split('-')[1];
      const sizeValue = this.getAttribute(sizeAttr) || defaultScreenSizes[sizeType.toUpperCase()];
      const coords = sizeValue.split('x').map(val => val.trim());
      if (coords.length) {
        const [width, height] = coords;
        return {
          ...acc,
          [sizeType]: { width, height },
        };
      }
      return {
        ...acc,
        [sizeType]: { width: '100%', height: '100%' },
      };
    }, {});
  }

  extractResolutionAttribute() {
    const width = this.getAttribute(ATTRIBUTES.resolutionWidth);
    const height = this.getAttribute(ATTRIBUTES.resolutionHeight);
    
    return width || height ? pickBy({ width, height }, identity) : null;
  }

  handleCommentsToggle(payload) {
    this.setAttribute('comments-opened', payload);
    this.onCommentsClicked(payload);
  }

  render() {
    const locale = this.getAttribute(ATTRIBUTES.locale) || 'en';
    const messages = locale === 'it' ? itLocale.messages : enLocale.messages;

    const screenSizes = this.extractScreenSizeAttribute();
    const resolution = this.extractResolutionAttribute();

    const params = pickBy({
      appBuilderDomain: this.getAttribute(ATTRIBUTES.appBuilderDomain),
      languages: JSON.parse(this.getAttribute(ATTRIBUTES.languages)),
      currentLang: this.getAttribute(ATTRIBUTES.currentLang),
      screenSizes,
      resolution,
      userName: this.getAttribute(ATTRIBUTES.userLogged),
      onClickLogout: this.onLogoutClicked,
      onCommentsToggle: this.handleCommentsToggle,
      onResolutionChange: this.onChangeResolution,
      onShareClicked: this.onShareClicked,
      onChangeLang: this.onChangeLanguage,
    }, identity);

    params.hasPermissionReview = this.getAttribute(ATTRIBUTES.hasPermissionReview) === 'true';
    params.commentsOpened = this.getAttribute(ATTRIBUTES.commentsOpened) === 'true';

    ReactDOM.render(
      <IntlProvider locale={locale} messages={messages}>
        <PreviewControlBar {...params} />
      </IntlProvider>,
      this,
    );
  }

  disconnectedCallback() {
    if (this.unsubscribeFromCommentCloseEvents) {
      this.unsubscribeFromCommentCloseEvents();
    }
  }
}

if (!customElements.get('preview-control-bar')) {
  customElements.define('preview-control-bar', PreviewControlBarElement);
}

export default PreviewControlBarElement;
