import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { FormattedMessage } from 'react-intl';
import { pick } from 'lodash';
import { Icon, Button } from 'patternfly-react';
import { ToggleButtonGroup, ToggleButton, NavDropdown, MenuItem } from 'react-bootstrap';

import 'components/preview-control-bar/PreviewControlBar.scss';

const defaultResolution = { width: '100%', height: '100%' };

const conditionAddPx = val => /%$/.test(val) || /px$/.test(val) ? val : `${val}px`;

const PreviewControlBar = ({
  appBuilderDomain,
  userName,
  resolution,
  onClickLogout,
  commentsOpened,
  onCommentsToggle,
  screenSizes,
  onResolutionChange,
  onShareClicked,
  languages,
  currentLang,
  onChangeLang,
  hasPermissionReview,
}) => {
  const [commentOpened, setCommentOpened] = useState(commentsOpened);
  useEffect(() => {
    setCommentOpened(commentsOpened);
  }, [commentsOpened])


  const { width, height } = pick({ ...defaultResolution, ...resolution }, Object.keys(defaultResolution));
  const [resoWidth, setResoWidth] = useState(conditionAddPx(width));
  const [resoHeight, setResoHeight] = useState(conditionAddPx(height));

  const entandoLogo = (
    <svg width="45" height="45" viewBox="0 0 45 45" fill="none" xmlns="http://www.w3.org/2000/svg">
      <path d="M45 0H0V45H45V0Z" fill="#101820"/>
      <path d="M17.3055 34.6051H10.3819V27.6943L6.92334 29.3143V38.0636H10.3819H17.2991L17.3055 34.6051Z" fill="#00A0DF"/>
      <path d="M27.6943 10.395H34.6179V17.3057L38.0765 15.6857V6.9364H34.6179H27.7008L27.6943 10.395Z" fill="#00A0DF"/>
      <path d="M23.9916 24.4865C28.0801 24.4865 29.5844 22.6029 29.5844 20.3786V18.45C29.5844 16.2322 27.9194 13.8407 24.1459 13.8407H21.1566C19.6908 13.7938 18.266 14.3285 17.1929 15.3281C16.1199 16.3276 15.4857 17.7111 15.4287 19.1765C15.4287 19.755 15.4287 24.0107 15.4287 24.4865C15.4287 27.7715 18.4951 31.1529 22.8987 31.1529C25.276 31.1823 27.575 30.304 29.3273 28.6972L27.3151 25.8943C25.4959 27.135 24.9944 27.6815 23.0209 27.6815C21.0473 27.6815 18.8873 26.5822 18.8873 24.4672L23.9916 24.4865ZM23.9916 17.3122C25.7016 17.3122 26.1001 17.82 26.1001 18.9257V19.6779C26.1001 21.1436 25.4573 21.0215 23.9466 21.0215H18.8873V19.1765C18.8855 18.9184 18.9375 18.6628 19.04 18.4259C19.1424 18.1891 19.2931 17.9762 19.4824 17.8008C19.6717 17.6254 19.8955 17.4914 20.1395 17.4072C20.3834 17.3231 20.6422 17.2907 20.8994 17.3122H23.9916Z" fill="white"/>
    </svg>
  );

  const userMenuTitle = (
    <span>
      <Icon name="user" /> { userName }
    </span>
  );

  const onCommentsClicked = () => {
    const opened = !commentOpened;
    setCommentOpened(opened);
    onCommentsToggle(opened);
  };

  const sizeSelected = ({ width, height }) => () => {
    setResoWidth(conditionAddPx(width));
    setResoHeight(conditionAddPx(height));
    onResolutionChange({ width: conditionAddPx(width), height: conditionAddPx(height) });
  };

  const handleWidthChange = ev => setResoWidth(ev.target.value);
  const handleHeightChange = ev => setResoHeight(ev.target.value);

  const widthKeyUp = (ev) => {
    if (ev.keyCode === 13) {
      const width = conditionAddPx(ev.target.value);
      setResoWidth(width);
      onResolutionChange({ width, height: resoHeight });
    }
  };

  const heightKeyUp = (ev) => {
    if (ev.keyCode === 13) {
      const height = conditionAddPx(ev.target.value);
      setResoHeight(height);
      onResolutionChange({ width: resoWidth, height });
    }
  };

  return (
    <div className="PreviewControlBar">
      <nav>
        <ul>
          <li className="leftArea">
            {entandoLogo}
            <a href={`${appBuilderDomain}dashboard`} target="_blank" rel="noopener noreferrer" className="PreviewControlBar__dashboard">
              <Icon size="lg" name="home" /><FormattedMessage id="preview.controlbar.dashboard" />
            </a>
            {hasPermissionReview && (
              <Button className={clsx('PreviewControlBar__commentsBtn', commentOpened && 'opened')} onClick={onCommentsClicked}><Icon size="lg" name="comments" /></Button>
            )}
          </li>

          <li className="middleArea">
            <Button className="PreviewControlBar__deviceBtn" onClick={sizeSelected(screenSizes.desktop)}><Icon size="lg" name="desktop" /></Button>
            <Button className="PreviewControlBar__deviceBtn" onClick={sizeSelected(screenSizes.mobile)}><Icon size="lg" name="mobile" /></Button>
            <Button className="PreviewControlBar__deviceBtn" onClick={sizeSelected(screenSizes.tablet)}><Icon size="lg" name="tablet" /></Button>
            <input className="custom-width" onChange={handleWidthChange} value={resoWidth} onKeyUp={widthKeyUp} />
            x
            <input className="custom-height" onChange={handleHeightChange} value={resoHeight} onKeyUp={heightKeyUp} />
          </li>

          <li className="rightArea">
            <div className="LanguageSelect">
              <select
                className="LanguageSelect__dropdown"
                defaultValue={currentLang}
                onChange={e => onChangeLang(e.target.value)}
              >
                {languages.map(lang => (
                  <option value={lang.code} className="LanguageSelect__option">{lang.code.toUpperCase()}</option>
                ))}
              </select>
              <span className="LanguageSelect__icon caret" />
            </div>
            <ToggleButtonGroup type="radio" name="pageType" className="PreviewControlBar__pageType">
                <ToggleButton value="preview"><FormattedMessage id="preview.controlbar.tabpreview" /></ToggleButton>
                <ToggleButton disabled value="published"><FormattedMessage id="preview.controlbar.tabpublished" /></ToggleButton>
            </ToggleButtonGroup>
          </li>

          <li className="rightArea">
            <Button className="PreviewControlBar__share" onClick={onShareClicked}><Icon name="user-plus" size="lg" /> 
              <FormattedMessage id="preview.controlbar.share" />
            </Button>
          </li>

          <NavDropdown id="previewControlBarUserMenu" className="PreviewControlBar__userDropdown" title={userMenuTitle} pullRight>
            <MenuItem href={`${appBuilderDomain}myProfile`}><Icon name="user" /> <FormattedMessage id="preview.controlbar.myprofile" /></MenuItem>
            <MenuItem onClick={onClickLogout}><Icon name="sign-out" /> <FormattedMessage id="preview.controlbar.logout" /></MenuItem>
          </NavDropdown>
          
        </ul>
      </nav>
    </div>
  );
}

const SizePropType = PropTypes.shape({
  width: PropTypes.string,
  height: PropTypes.string,
});

PreviewControlBar.propTypes = {
  appBuilderDomain: PropTypes.string,
  userName: PropTypes.string,
  onClickLogout: PropTypes.func,
  screenSizes: PropTypes.shape({
    desktop: SizePropType,
    mobile: SizePropType,
    tablet: SizePropType,
  }).isRequired,
  resolution: SizePropType,
  commentsOpened: PropTypes.bool,
  onCommentsToggle: PropTypes.func,
  onShareClicked: PropTypes.func,
  onResolutionChange: PropTypes.func,
  onChangeLang: PropTypes.func,
  languages: PropTypes.arrayOf(PropTypes.shape({
    code: PropTypes.string,
  })),
  currentLang: PropTypes.string,
  hasPermissionReview: PropTypes.bool,
};

PreviewControlBar.defaultProps = {
  appBuilderDomain: '//',
  userName: 'user',
  onClickLogout: () => {},
  resolution: {},
  commentsOpened: false,
  onCommentsToggle: () => {},
  onResolutionChange: () => {},
  onShareClicked: () => {},
  onChangeLang: () => {},
  languages: [],
  currentLang: '',
  hasPermissionReview: false,
};

export default PreviewControlBar;
