import React from 'react';
import { FormattedMessage, injectIntl, intlShape } from 'react-intl';
import PropTypes from 'prop-types';
import { Icon, Button } from 'patternfly-react';

import 'components/preview-comments/PreviewCommentsBar.scss';

const selectTypeOptionKeys = ['all', 'yours', 'resolved'];

const PreviewCommentsBar = ({ intl, onCommentClose }) => (
  <>
    <div className="PreviewCommentsBar__caption">
      <Icon size="lg" name="comments" className="PreviewCommentsBar__caption-icon" />
      <h2><FormattedMessage id="preview.commentbar.caption" /></h2>
      <div className="PreviewCommentsBar__select-type">
        <select
          className="PreviewCommentsBar__select-type--dropdown"
        >
          {selectTypeOptionKeys.map(typeOption => (
            <option value={typeOption} className="PreviewCommentsBar__select-type--option">
              {intl.formatMessage({ id: `preview.commentbar.commenttype.${typeOption}` })}
            </option>
          ))}
        </select>
        <span className="PreviewCommentsBar__select-type--icon caret" />
      </div>
      <Button onClick={() => onCommentClose()} className="PreviewCommentsBar__close"><Icon size="lg" name="close" /></Button>
    </div>
    <div className="PreviewCommentsBar__area" />
  </>
);

PreviewCommentsBar.propTypes = {
  onCommentClose: PropTypes.func,
  intl: intlShape.isRequired,
  hasPermissionReview: PropTypes.bool,
};

PreviewCommentsBar.defaultProps = {
  onCommentClose: () => {},
  hasPermissionReview: false,
};

export default injectIntl(PreviewCommentsBar);
