import React from 'react';
import ReactDOM from 'react-dom';
import { IntlProvider } from 'react-intl';
import { enLocale, itLocale } from 'locale';

import { createWidgetEvent } from 'utils/CustomEventUtils';
import PreviewCommentsBar from 'components/preview-comments/PreviewCommentsBar';

const ATTRIBUTES = {
  locale: 'locale',
  hasPermissionReview: 'has-permission-review',
};

export const PreviewCommentsBarEvent = {
  COMMENT_CLOSE: 'preview-comments-bar/on-comment-close',
};

window.PreviewCommentsBarEvent = PreviewCommentsBarEvent;

class PreviewCommentsBarElement extends HTMLElement {
  constructor(props) {
    super(props);
    this.onCommentClose = createWidgetEvent(this, PreviewCommentsBarEvent.COMMENT_CLOSE);
  }

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  attributeChangedCallback(attribute, oldValue, newValue) {
    if (!Object.values(ATTRIBUTES).includes(attribute)) {
      throw new Error(`Untracked changed attribute: ${attribute}`);
    }
    if (newValue !== oldValue) {
      this.render();
    }
  }

  connectedCallback() {
    setTimeout(() => this.render(), 500);
  }

  render() {
    const locale = this.getAttribute(ATTRIBUTES.locale) || 'en';
    const messages = locale === 'it' ? itLocale.messages : enLocale.messages;

    const params = {
      hasPermissionReview: this.getAttribute(ATTRIBUTES.hasPermissionReview) === 'true',
      onCommentClose: this.onCommentClose,
    };

    ReactDOM.render(
      <IntlProvider locale={locale} messages={messages}>
        <PreviewCommentsBar {...params} />
      </IntlProvider>,
      this,
    );
  }
}

if (!customElements.get('preview-comments-bar')) {
  customElements.define('preview-comments-bar', PreviewCommentsBarElement);
}

export default PreviewCommentsBarElement;
